<template>
  <div class="container">
    <div class="section1" v-if="show">
      <div class="add-item" @click="addItem">
        <van-icon name="add-o" size="30" color="#1989fa" />
      </div>
      <div class="item" v-for="(item, index) in itemList" :key="index">
        <van-cell-group>
          <van-dropdown-menu v-if="role > 0">
            <van-dropdown-item
              @change="changeStore(index)"
              v-model="value1"
              :options="option1"
            />
          </van-dropdown-menu>
          <van-field
            v-else
            v-model="userName"
            label=""
            placeholder=""
             @input="changeUserName(index)"
          />
          <van-field
            v-model="item.name"
            label=""
            placeholder=""
          />
          <van-field
            v-model="item.material"
            label=""
            placeholder=""
          />
          <van-field
            v-model="item.number"
            label=""
            placeholder=""
            type="number"
          />

          <van-field
            v-model="item.remark"
            rows="2"
            autosize
            label=""
            type="textarea"
            maxlength="50"
            placeholder=""
            show-word-limit
          />
          <div class="flex-sa">
            <van-field
              v-model="item.length"
              label=""
              placeholder=" "
              type="number"
            />
            <van-field
              v-model="item.width"
              label=""
              placeholder=""
              type="number"
            />
          </div>
          <van-cell title=""  :value="item.com_time" @click="Clickshowtime(index)" />
          <van-calendar v-model="showtime" placeholder="" @confirm="onConfirmtime"  />
        </van-cell-group>

        <div v-if="item.id != ''" @click="savePro(index)" class="save-item">
          
        </div>
        <div v-else class="flex-sa">
          <div @click="savePro(index)" class="save-item"></div>
          <div v-if="index > 0" @click="remove(index)" class="save-item">
            
          </div>
        </div>
      </div>

      <div class="add-btn">
        <van-button size="normal" @click="switchStatus" block round type="info"
          ></van-button
        >
      </div>
    </div>
    <div class="section2" v-if="!show">
      <div class="item">
        <van-collapse v-model="activeNames">
          <van-collapse-item
            :title="sitem.name"
            :name="sindex"
            v-for="(sitem, sindex) in saveItemList"
            :key="sindex"
          >
            <van-cell
              center
              :title="item.pname"
              v-for="(item, index) in sitem.processList"
              :key="index"
            >
              <template #right-icon>
                <van-switch
                  :value="item.checked"
                  @input="onInput(item.checked, sindex, index)"
                  size="24"
                />
              </template>
            </van-cell>
          </van-collapse-item>
        </van-collapse>
      </div>
      <!-- <div class="item">
        <van-field name="radio" label="">
          <template #input>
            <van-radio-group v-model="radio" direction="horizontal">
              <van-radio name="1"></van-radio>
              <van-radio name="2"></van-radio>
            </van-radio-group>
          </template>
        </van-field>
      </div> -->

      <div class="add-btn">
        <van-button @click="switchStatus" size="normal" block round type="info"
          ></van-button
        >
        <van-button @click="subPro" size="normal" block round type="info"
          ></van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { addPorder, getPro, addPcart, getUsers } from "@api/public";
import {
  Button,
  DropdownMenu,
  DropdownItem,
  Calendar,
  RadioGroup,
  Switch,
  Cell,
  Radio,
  Collapse,
  Field,
  CollapseItem,
  Icon,
} from "vant";
import localStorage from "@utils/store/localStorage";
import Loadings from "@components/Loadings";
export default {
  name: "Index",
  components: {
    [Calendar.name]: Calendar,
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
    [Button.name]: Button,
    [CollapseItem.name]: CollapseItem,
    [Cell.name]: Cell,
    [Collapse.name]: Collapse,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Icon.name]: Icon,
    Loadings,
  },
  props: {},
  data: function () {
    return {
      value1: 0,
      showtime: false,
      option1: [{ text: "", value: 0 }],
      itemList: [
        {
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          remark: "",
          user_name: "",
          uid: "",
          com_time: "",
          id: "",
        },
      ],
      saveItemList: [],
      showPicker: false,
      processList: [],
      activeNames: [0],
      show: true,
      pshow: true,
      jsonData: null,
      uid: 0,
      userName: "",
      userList: [],
      role: 0,
      editInd:0,
      comTime:''
    };
  },
  created() {
    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.jsonData = jsonData;

    getPro({})
      .then(({ data }) => {
        data.forEach((item) => {
          item.checked = false;
        });

        this.processList = data;
      })
      .catch((e) => {
        this.$dialog.message(e.msg);
      });
    this.role = jsonData.role;
    if (jsonData.role > 0) {
      getUsers({ type: 0 })
        .then(({ data }) => {
          var option = [{ text: "", value: 0 }];
          this.userList = data;
          data.forEach((item, index) => {
            var obj = {};
            obj["text"] = item.signature ? item.signature : item.user_login;
            obj["value"] = index + 1;
            option.push(obj);
          });
          this.option1 = option;
        })
        .catch((e) => {
          this.$dialog.message(e.msg);
        });
    } else {
      this.uid = jsonData.id;
      this.userName = jsonData.signature
        ? jsonData.signature
        : jsonData.user_login;

      var itemList = this.itemList;
      itemList[0]["uid"] = jsonData.id;
      itemList[0]["userName"] = jsonData.id;
      itemList[0]["user_name"] = jsonData.signature
        ? jsonData.signature
        : jsonData.user_login;
      this.itemList = itemList;
    }
  },
  mounted: function () {},
  methods: {
    changeStore(e) {
      var itemList = this.itemList;
      itemList[e]["uid"] = this.userList[this.value1 - 1].id;
      itemList[e]["user_name"] = this.userList[this.value1 - 1].signature
        ? this.userList[this.value1 - 1].signature
        : this.userList[this.value1 - 1].user_login;
      this.itemList = itemList;
    },
    changeUserName(e){
      var itemList = this.itemList;
      itemList[e]["userName"] = this.userName;
      itemList[e]["user_name"] = this.userName;
      this.itemList = itemList;
    },
    remove(e) {
      var itemList = this.itemList;
      itemList.splice(e, 1);
      this.itemList = itemList;
    },
    savePro(e) {
      if (this.itemList[e]["user_name"] == "") {
        this.$dialog.message("");
      } else if (this.itemList[e]["name"] == "") {
        this.$dialog.message("");
      } else if (this.itemList[e]["material"] == "") {
        this.$dialog.message(" ");
      } else if (this.itemList[e]["number"] == "") {
        this.$dialog.message(" ");
      } else if (this.itemList[e]["width"] == "") {
        this.$dialog.message(" ");
      } else if (this.itemList[e]["length"] == "") {
        this.$dialog.message(" ");
      }else if (this.itemList[e]["com_time"] == "") {
        this.$dialog.message(" ");
      }  else {
        addPorder(this.itemList[e])
          .then(({ data }) => {
            var datas = data;
            datas["processList"] = this.processList;
            datas["uid"] = this.uid;
            this.itemList[e]["id"] = data.id;
            this.saveItemList[e] = datas;
            console.log(this.saveItemList);
            this.$dialog.message("");
          })
          .catch((e) => {
            this.$dialog.message(e.msg);
          });
      } 
    },
    onConfirmtime(date){
      this.comTime =  `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
      this.showtime = false;
      var itemList = this.itemList;
      itemList[this.editInd]['com_time'] =  this.comTime;

      this.itemList = itemList;
    },
    Clickshowtime(ind){
      this.showtime = true;
      this.editInd = ind;
    },
    subPro() {
      console.log(this.saveItemList);
      var data = [];
      this.saveItemList.forEach((item) => {
        var obj = {};
        var arr = [];
        obj["oid"] = item.id;
        item.processList.forEach((sitem) => {
          if (sitem.checked) {
            arr.push(sitem.pid);
          }
        });
        obj["proid"] = arr;
        data.push(obj);
      });

      addPcart({ data })
        .then(({ data }) => {
          this.$dialog.message("");
          location.reload();
        })
        .catch((e) => {
          this.$dialog.message(e.msg);
        });
    },
    addItem() {
      if (this.itemList.length == this.saveItemList.length) {
        var list = this.itemList;
        list.push({
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          id: "",
          user_name: this.userName,
          uid: this.uid,
          remark: "",
          com_time: "",
        });
        this.itemList = list;
      } else {
        this.$dialog.message(" ");
      }
    },
    switchStatus() {
      if (this.saveItemList.length > 0) {
        this.show = !this.show;
      } else {
        this.$dialog.message("");
      }
    },
    onInput(e, inds, ind) {
      var saveItemList = this.saveItemList;

      saveItemList[inds]["processList"][ind]["checked"] = !e;
      this.saveItemList = saveItemList;
      console.log(e);
      console.log(inds);
      console.log(ind);
      console.log(saveItemList);
    },
  },
};
</script>
<style scoped>
[v-cloak] {
  display: none;
}
button {
  margin-bottom: 15px;
}
.container {
  background: #f9f9f9;
}
.item {
  padding: 10px 20px 0;
}
.flex-sa {
  background: #fff;
}
.save-item {
  text-align: center;
  background: #fff;
  width: 100%;
  color: #666;
  line-height: 45px;
}
.add-btn {
  margin: 20px;
  margin-top: 40px;
}
.add-item {
  text-align: center;
  padding-top: 10px;
}
.container {
  padding-bottom: 60px;
}
</style>