<template>
  <div style="touch-action: none;">
    <div
      class="home"
      style="position:fixed;bottom:50px;right:50px;"
      id="right-nav"
      @touchmove="touchmove($event)"
    >
      <div @click="open" class="pictrueBox">
        <div class="pictrue"></div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { Icon } from "vant";
export default {
  name: "Home",
  props: {},
  components: {
    [Icon.name]: Icon,
  },
  data: function () {
    return {
      top: "",
      bottom: "",
    };
  },
  computed: mapGetters(["homeActive"], Icon),
  methods: {
    touchmove(event) {
      this.bottom = "auto";
      event.preventDefault();
      let top =
        event.touches[0].pageY -
        (document.documentElement.scrollTop || document.body.scrollTop) -
        this.$el.clientHeight;
      if (top > document.body.clientHeight - 150) {
        top = document.body.clientHeight - 150;
      } else if (top < 55) top = 55;
      this.top = top;
    },
    open: function () {
      this.$router.go(-1);
    },
  },
  created() {
    this.bottom = "50px";
  },
};
</script>

<style scoped>
.pictrueBox {
  width: 1.3rem;
  height: 1.2rem;
}
.pictrue {
  background: #fff;
  width: 50px; 
  height: 50px;
  border-radius: 50px;
  box-shadow: 0 0 10px #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}
</style>
